@import url(https://fonts.googleapis.com/css?family=Girassol|Pacifico&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background: rgb(0,0,0);

}

img {
    width: 200px;
    height: 220px;
    box-shadow: 0 10px 10px rgba(0, 0, 0, 0.425), 0 10px 10px rgba(0, 0, 0, 0.425);
    margin: 1rem;
  }
  
  img:hover {
    box-shadow: 0 0 2px 1px rgba(5, 68, 88, 0.5);
    -webkit-transform: scale(1.1, 1.1);
            transform: scale(1.1, 1.1);
    cursor: pointer;
  }
  
.modal-container {
    margin-top: 10rem;
  
  }
  
  .modal-title {
    margin-left: 10rem;
    color: rgb(230, 9, 9);


  }
  
  p {
    text-align: center;
    font-size: 2.5rem;
  }
  
.footer-title {
    background-color: #3a069b;
    height: 5rem;
    color: white;
  }
  
  .footer-bar {
    margin-top: 2rem;
    background-color: #864fec;
    height: 3rem;
  }
  
  h3 {
    padding: 2rem;

  }
  
.wrong {
    color: #fd0808;
    text-shadow: 2px 2px black;
  }
  
  .right {
    color: #05b353;
  }
  
  .navbar-container {
    background: rgb(28,170,185);
    background: linear-gradient(207deg, rgba(28,170,185,0.4948354341736695) 20%, rgba(3,30,37,1) 100%);
    font-size: 1.5rem;
    padding-left: 5rem;
    padding-right: 5rem;
    height: 9rem;
    font-size: 2rem;
  
  }
  
  .nav-title {
    font-size: 4rem;
    text-shadow: 4px 4px black;
    /* font-family: 'Girassol', cursive; */
    font-family: 'Pacifico', cursive;
  }
  
  .navbar-light .navbar-text {
    color: white;
  }
  
.justify-content-center {
  font-size: 1.3rem;
  text-align: center;
}
.img-container {
    padding-top: 10rem;
    display: flex;
    flex-wrap: wrap;
    max-width: 1000px;
    margin: 0 auto;
  }


  
